import board from '../models/Board'
import { useRef, useState } from 'react'

const ModalMenu = ({onClick}) => {

    const [hidden, setHidden] = useState(false)
    const [copied, setCopied] = useState(false)
    const link = window.location.href;
    const username = useRef("")

    const handleClick = () =>{
        if(String(username.current.value).length > 2){
            board.setUsername(username.current.value)
            setHidden(true)
            onClick()
        }
    }
    
    const copyLink = () =>{
        setCopied(true)
        navigator.clipboard.writeText(link)
    }

    return(
        <div className={hidden ? `hidden` : `modal-container`}>
            <div className="modalw">
                <div className="modal-head">Игра в НАРДЫ</div>
                <div className="modal-body">
                    <div className="modal-link" onClick={copyLink}>Ссылка для друга: {link}</div>
                    <div className={copied ? 'copied' : 'hidden'}>СКОПИРОВАНО</div>
                    <div className="modal-button-block">  
                        <input className='modal-input' ref={username} type="text" placeholder={"Введите на100ящее имя"}/>
                        <button onClick={handleClick} className="modal-button">Да меня реально так зовут</button>
                    </div>
                    <div className="modal-text">ВНИМАНИЕ:
                    После входа занимай
                    сторону нажатием на картинку, если ничего не происходит нажимай дальше сервер бесплатный
                    ему надо включиться по запросу крч когда ник встанет на место стороны, кидай ссылку другу)
                    </div>
                </div>                
            </div>
        </div>
    )
}

export default ModalMenu;